<template>
  <div class="nursingplanprint">
    <div v-for="(pData, index) in newPrintData" :key="index">
      <table class="form_title" border="2px #000000 solid;" width="100%">
        <thead>
          <tr>
            <td colspan="18">
              <v-row>
                <v-col cols="3">
                  <h3>{{ cData.company_name }}</h3>
                </v-col>
                <v-col cols="6" class="text-center">
                  <h1>
                    <strong>護理計畫單</strong>
                  </h1>
                </v-col>
                <v-col cols="3"></v-col>
              </v-row>
            </td>
          </tr>
          <tr>
            <th colspan="4" class="pa-2">
              <v-row>
                <v-col cols="2">病歷號: {{ uData.p_code }}</v-col>
                <v-col cols="2">床號: {{ uData.bed_name }}</v-col>
                <v-col cols="2">姓名: {{ uData.name }}</v-col>
                <v-col cols="2">性別: {{ uData.sex }}</v-col>
                <v-col cols="4"
                  >開始~停止: {{ filterStartToStop(pData) }}</v-col
                >
                <!-- <v-col cols="2">生日: {{uData.birthday}}</v-col> -->
                <!-- <v-col cols="2">年齡: {{age}}</v-col> -->
                <!-- <v-col cols="12">疾病史: {{uData.hadadisease}}</v-col> -->
              </v-row>
            </th>
          </tr>
        </thead>

        <tbody>
          <tr>
            <th colspan="4" class="text-center pa-2">護理診斷</th>
          </tr>
          <tr>
            <td colspan="4">{{ pData[0].question }}</td>
          </tr>

          <tr>
            <th colspan="4" class="text-center pa-2">可能導因</th>
          </tr>
          <tr>
            <!-- <td colspan="2">{{pData[0].relevant}}</td> -->
            <td colspan="4">
              <div
                v-for="item in reCreateDiffinfo(pData, 'relevant')"
                :key="item.snkey"
              >
                <v-sheet color="grey lighten-2"
                  >【{{ filterDateZone(item) }}】</v-sheet
                >
                {{ item[0].relevant }}
              </div>
            </td>
          </tr>

          <tr>
            <th colspan="4" class="text-center pa-2">護理措施</th>
          </tr>
          <tr>
            <!-- <td colspan="2" style="white-space:pre-wrap">{{pData[0].measures}}</td> -->
            <td colspan="4">
              <div
                v-for="item in reCreateDiffinfo(pData, 'measures')"
                :key="item.snkey"
              >
                <v-sheet color="grey lighten-2"
                  >【{{ filterDateZone(item) }}】</v-sheet
                >
                <span style="white-space: pre-wrap">{{
                  item[0].measures
                }}</span>
              </div>
            </td>
          </tr>

          <tr>
            <th colspan="4" class="text-center pa-2">護理目標</th>
          </tr>
          <tr>
            <!-- <td colspan="2" style="white-space:pre-wrap">{{pData[0].target}}</td> -->
            <td colspan="4">
              <div
                v-for="item in reCreateDiffinfo(pData, 'target')"
                :key="item.snkey"
              >
                <v-sheet color="grey lighten-2"
                  >【{{ filterDateZone(item) }}】</v-sheet
                >
                <span style="white-space: pre-wrap">{{ item[0].target }}</span>
              </div>
            </td>
          </tr>

          <tr>
            <th colspan="4" class="text-center pa-2">評值歷程</th>
          </tr>
          <tr class="text-center font-weight-bold">
            <th>評值日期</th>
            <th>評值說明</th>
            <th>紀錄人</th>
            <th>蓋章</th>
          </tr>

          <tr v-for="(item, index) in pData" :key="index">
            <!-- {{pData}} -->
            <td class="text-nowrap text-center" width="10%">{{ item.date }}</td>
            <td style="white-space: pre-wrap">{{ item.plan }}</td>
            <td class="text-nowrap">{{ item.create_man | filterName }}</td>
            <td width="6%"></td>
          </tr>
        </tbody>
      </table>

      <p style="page-break-after: always"></p>
      <!-- <pre>{{items}}</pre> -->
    </div>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  data() {
    return {
      cData: {},
      pData: {},
      uData: {},
      list: {}, //表單內資料
      items: [],

      newPrintData: [],
    };
  },

  filters: {
    filterName(val) {
      if (val != undefined) {
        var name = val.split("(");
        return name[0];
      }
    },
  },

  computed: {
    age() {
      return dayjs().format("YYYY") - dayjs(this.uData.birthday).format("YYYY");
    },
  },

  mounted() {
    this.cData = JSON.parse(sessionStorage.getItem("cData"));
    this.pData = JSON.parse(sessionStorage.getItem("pData"));
    this.uData = JSON.parse(sessionStorage.getItem("uData"));
    // this.list = JSON.parse(sessionStorage.getItem("printData"));
    this.items = JSON.parse(sessionStorage.getItem("printData"));

    //這裡要重新構造資料,因為有可能會有兩個以上的護理診斷(question)
    var questionArr = new Set();
    this.items.forEach((i) => {
      questionArr.add(i.question);
    });
    questionArr = [...questionArr];

    //組合成新的列印資料
    var newPrintData = [];
    questionArr.forEach((i) => {
      const matchData = this.items.filter((item) => {
        return item.question == i;
      });
      newPrintData.push(matchData);
    });

    this.newPrintData = newPrintData;
    // console.log(newPrintData);
    //這裡要重新構造資料,因為有可能會有兩個以上的護理診斷(question)
  },

  methods: {
    //可能導因的內容有不同時的判斷
    reCreateDiffinfo(pData, type) {
      var newArr = new Set();
      pData.forEach((i) => {
        newArr.add(i[type]);
      });
      newArr = [...newArr];

      //組合成新的列印資料
      var newPrintData = [];
      newArr.forEach((i) => {
        const matchData = pData.filter((item) => {
          return item[type] == i;
        });
        newPrintData.push(matchData);
      });

      return newPrintData;
    },
    filterDateZone(dates) {
      var start = "";
      var end = "";

      dates.forEach((item, index) => {
        if (index == 0) {
          start = item.date;
        }
        end = item.date;
      });
      return `${start} ~ ${end}`;
    },
    filterStartToStop(pData) {
      var start = "";
      var end = "";

      pData.forEach((item, index) => {
        if (index == 0) {
          start = item.date;
        }
        end = item.stop_date;
      });
      return `${start} ~ ${end}`;
    },
  },
};
</script>

<style lang="scss" scoped>
@import "node_modules/bootstrap/scss/bootstrap";
@import "node_modules/bootstrap-vue/src/index.scss";
.nursingplanprint {
  .form_title {
    font-size: 1.2rem;
    font-family: "微軟正黑體";
  }

  td {
    padding: 4px;
    // border: 1px #000000 solid;
  }
}

@media print {
  .nursingplanprint {
    .form_title {
      font-size: 0.8rem;
      font-family: "微軟正黑體";
    }
  }

  @page {
    size: A4 portrait;
    margin: 0cm;
    padding: 0cm;
  }
}
</style>